body {
  background-color: #d7d7d7;
}

.container {
  width: 1200px;
  margin: 50px auto;
}

.section {
  width: 560px;
  background-color: #fafafa;
  border-radius: 20px;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  padding: 30px;
  display: flex;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, .1);
}

img {
  object-fit: cover;
  max-width: 450px;
  justify-content: center;
  padding-top: 30px;
  display: flex;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  background: linear-gradient(0deg, rgba(255, 61, 0, .2) 33%, #ff3d00 100%);
  border-radius: 50%;
  margin: 30px auto;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  background: #fafafa;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loader-text {
  text-align: center;
}

#dog-loader {
  max-width: 250px;
  max-height: 250px;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

